jidas o de punto. Las características de la construcción de estas darán diferentes propiedades a las telas. En este sentido, no todas las que se utilizan como entretelas son producidas para tal fin. Hay algunos tipos de forros, batistas, muselinas, e inclusive la misma tela exterior, que pueden usarse como entretela.")])]),_c('li',[_c('i',{staticClass:"lista-ul__vineta"}),_c('p',{staticClass:"mb-0"},[_c('strong',[_vm._v("Entretelas no tejidas")]),_vm._v(": por lo general, están hechas de poliéster, rayón, nailon o una mezcla de diferentes fibras, las cuales son distribuidas al azar y juntadas por acción química o térmica. Ya que no tienen una dirección de hilo o grano, no se desmechan y pueden ser cortadas en cualquier dirección o ángulo. Hay que ser muy cuidadoso para seleccionar la más apropiada.")])]),_c('li',[_c('i',{staticClass:"lista-ul__vineta"}),_c('p',{staticClass:"mb-0"},[_c('strong',[_vm._v("Entretelas fusionables")]),_vm._v(": son diseñadas para proveer las ventajas de la entretela cuando esté unida a la superficie interna de la tela externa, bajo la influencia de temperatura, tiempo y presión. De hecho, la adherencia de la entretela fusionable a la tela externa produce un laminado nuevo a la tela, en el cual las características de la entretela se agregan a las de la tela externa.")])]),_c('li',[_c('i',{staticClass:"lista-ul__vineta"}),_c('p',{staticClass:"mb-0"},[_c('strong',[_vm._v("Entretelas de punto o malla")]),_vm._v(": están disponibles por inserción de trama, inserción de urdimbre y tricota. Las de nailon tienen estiramiento a lo ancho y estabilidad a lo largo. Este tipo añade forma y cuerpo a prácticamente cualquier tela, tejida o de punto.")])]),_c('li',[_c('i',{staticClass:"lista-ul__vineta"}),_c('p',{staticClass:"mb-0"},[_c('strong',[_vm._v("Entretelas tejidas")]),_vm._v(": pueden ser de algodón, rayón, lana, poliéster o mezclas de fibras. Se tejen de la misma forma que las telas normales (urdimbre / trama). Usualmente, cuando se cortan al se